'use client';

import { stringify } from 'querystring';
import React, { createContext, useContext, useEffect, useRef, useState } from 'react';

type ChatContextType = {
  userName: string;
  messages: { sender: string; text: string }[];
  sendMessage: (text: string) => void;
};

const ChatContext = createContext<ChatContextType | undefined>(undefined);

const adjectives = ['Swift', 'Dark', 'Flying', 'Sneaky', 'Mighty', 'Electric'];
const animals = ['Panther', 'Tiger', 'Fox', 'Eagle', 'Shark', 'Wolf'];

function generateRandomName() {
  const adj = adjectives[Math.floor(Math.random() * adjectives.length)];
  const animal = animals[Math.floor(Math.random() * animals.length)];
  const number = Math.floor(1000 + Math.random() * 9000);
  return `${adj} ${animal} #${number}`;
}

export const ChatProvider = ({ children }: { children: React.ReactNode }) => {
  const [userName, setusername] = useState<string>(stringify);
  const [messages, setMessages] = useState<{ sender: string; text: string }[]>([]);
  const socketRef = useRef<WebSocket | null>(null);

  useEffect(() => {
    const socket = new WebSocket('ws://localhost:4000');
    socketRef.current = socket;
    setusername(generateRandomName())
    socket.onmessage = (event) => {
      const data = JSON.parse(event.data);
      setMessages((prev) => [...prev, data]);
    };

    return () => socket.close();
  }, []);

  const sendMessage = (text: string) => {
    const message = { sender: userName, text };
    setMessages((prev) => [...prev, message]);
    socketRef.current?.send(JSON.stringify(message));
  };

  return (
    <ChatContext.Provider value={{ userName, messages, sendMessage }}>
      {children}
    </ChatContext.Provider>
  );
};

export const useChat = () => {
  const context = useContext(ChatContext);
  if (!context) throw new Error('useChat must be used within a ChatProvider');
  return context;
};
